*{
    padding: 0;
    margin: 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

*::after{
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

*::before {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

ul, ol, dl, li {
    list-style-type: none;
}

a {
    text-decoration: none;
    color: inherit;
}


